* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.snakegame {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 100vh;
}

.board {
    grid-column: span 9;
    border: 0px solid #222b00ff;
}

.board > .fruit {
    border-radius: 50%;
    background: #88aa00ff;
    position: absolute;
}

.board > .snake > .head, .board > .snake > .body {
    background: #88aa00ff;
    position: absolute;
}

.board > .snake > .head {
    background: #445500ff;
}

.controls {
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: repeat(3, 1fr);

    grid-column: span 3;
    background: #445500ff;
    text-align: center;
}

.controls > .status {
    grid-row: 1;
    padding-top: 10px;
}

.controls > .status > h1 {
    padding: 2px;
    color: #aad400ff;
    font-size: 36px;
}

.controls > .status > h2 , .controls > .status > h3{
    padding: 5px;
    color: #668000ff;
    font-size: 34px;
}

.controls > .controls-movement {
    grid-row: 2;
}

.controls > .controls-movement > button {
    display: inline-block;
    width: 80%;
    margin: 5px auto;
    padding: 10px;
    border: none;
    border-radius: 5px;
    background: #668000ff;
    color: #88aa00ff;
    font-size: 28px;
}

.controls > .controls-movement > button:hover {
    cursor: pointer;
    background: rgb(97, 121, 0);
}

.controls > .controls-movement > button:focus {
    outline: none;
}

.controls > .controls-direction {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);

    grid-row: 3;
    width: 80%;
    margin: 0px auto;
}

.controls > .controls-direction > .ArrowUp {
    grid-column: 2;
    grid-row: 1;
    justify-self: center;
    align-self: end;
    transform: rotate(-90deg);
}

.controls > .controls-direction > .ArrowRight {
    grid-column: 3;
    grid-row: 2;
    justify-self: left;
    align-self: center;
}
.controls > .controls-direction > .ArrowDown {
    grid-column: 2;
    grid-row: 3;
    justify-self: center;
    align-self: start;
    transform: rotate(90deg);
}

.controls > .controls-direction > .ArrowLeft {
    grid-column: 1;
    grid-row: 2;
    justify-self: right;
    align-self: center;
    transform: rotate(180deg);
}

.controls > .controls-direction > button {
    width: 80%;
    height: 85%;
    background: #668000ff;
    border: none;
    border-radius: 5px;
    font-size: 28px;
    font-weight: bold;
    color: #88aa00ff;
}

.controls > .controls-direction > button:hover {
    cursor: pointer;
    background: rgb(97, 121, 0);
}

.controls > .controls-direction > button:focus {
    outline: none;
}

@media (max-width: 1024px) { /* Tablet */
    .board {
        grid-column: span 8;
    }

    .controls {
        grid-column: span 4;
    }

    .controls > .status > h1 {
        padding: 1px;
        font-size: 28px;
    }
    
    .controls > .status > h2 , .controls > .status > h3{
        padding: 2.5px;
        font-size: 24px;
    }
    
    .controls > .controls-movement > button {
        font-size: 18px;
    }

    .controls > .controls-direction > button {
        font-size: 18px;
    }
}

@media (max-width: 425px) { /* Mobile */
    .snakegame {
        grid-template-rows: 60vh 40vh;
    }

    .board {
        grid-column: span 12;
        grid-row: 1;
    }

    .controls {
        grid-column: span 12;
        grid-row: 2;

        grid-template-columns: 60% 40%;
        grid-template-rows: 2fr 1fr;
    }

    .controls > .status {
        grid-column: 1;
        grid-row: 1;
        align-self: center;
    }

    .controls > .controls-movement {
        grid-column: 1;
        grid-row: 2;
    }

    .controls > .controls-movement > button {
        width: 30%;
    }

    .controls > .controls-direction {
        grid-column: 2;
        grid-row: span 2;

        width: 100%;
        height: 70%;
        margin: auto 0px;
    }
}
